import styled from 'styled-components';
import { ThemeProps, Theme } from '../../../theme/theme.config';

export const StyledSection = styled.section`
  width: 100%;
  display: flex;
  justify-content: space-between;
  min-height: 100vh;
`;

export const TextContainer = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: ${({ theme }: ThemeProps) => theme.color.black};
`;

export const SectionTitle = styled.h2`
  font-size: 49px;
  margin-bottom: 20px;
`;

export const SectionDescription = styled.p`
  font-size: 21px;
`;

export const SectionImage = styled.img`
  flex: 1;
`;

export const SectionNumber = styled.h3.attrs(
  ({
    containerHeight,
    active = false,
  }: {
    containerHeight: number;
    active?: boolean;
  }) => ({ containerHeight, active })
)`
  top: 30px;
  min-width: 70px;
  min-height: 70px;
  max-width: 70px;
  max-height: 70px;
  text-align: center;
  line-height: 70px;
  color: ${({ theme }: ThemeProps) => theme.color.white};
  background-color: ${({ theme, active }: ThemeProps & { active: boolean }) =>
    active ? theme.color.primary : '#EBEBEB'};
  border-radius: 50%;
  margin: 0 20px;
  position: relative;
  font-size: 27px;

  ${(props) =>
    props.active &&
    `
  animation: setColor 1s 1 0s linear;
  `}

  @keyframes setColor {
    0% {
      background: #ebebeb;
    }
    100% {
      background: #044aaf;
    }
  }

  &::after {
    content: '';
    display: block;
    width: 6px;
    height: ${(props) => `${props.active ? props.containerHeight - 70 : 0}px`};
    background-color: ${({ theme, active }: ThemeProps & { active: boolean }) =>
      active ? theme.color.primary : '#EBEBEB'};
    position: absolute;
    top: 70px;
    left: 50%;
    transform: translateX(-50%);
    transition: height 1s ease;

    ${(props) =>
      props.active &&
      `
  animation: setColor 1s 1 0s linear;
  `}
  }
`;
